import "./styles.css";

var canvas = document.querySelector("canvas");
var button = document.querySelector("button");
var image = document.querySelector("img");
var ctx = canvas.getContext("2d");
ctx.imageSmoothingEnabled = false;

var rows = 4;
var cols = 3;

function drawFace() {
  ctx.clearRect(0, 0, 256, 256);
  drawPiece(0, Math.floor(Math.random() * cols));
  drawPiece(1, Math.floor(Math.random() * cols));
  drawPiece(2, Math.floor(Math.random() * cols));
  drawPiece(3, Math.floor(Math.random() * cols));
}

function drawPiece(row, col) {
  ctx.drawImage(image, col * 64, row * 64, 64, 64, 0, 0, 256, 256);
}

image.onload = () => {
  drawFace();
};

button.addEventListener("click", drawFace);
